<script>
import uniqid from "uniqid";
import { computed, ref } from "@vue/reactivity";
import { useStore } from "vuex";
import { onMounted } from "@vue/runtime-core";

export default {
  setup() {
    const store = useStore();
    const Swal = require("sweetalert2");

    const titulos = ref({
      tecnico: "",
      averia: "",
      solucion: "",
    });

    const idAveria = ref("");

    const arrayTecnicos = ref([]);
    const arrayAverias = ref([]);
    const arraySoluciones = ref([]);

    onMounted(() => {
      tecnicos();
      averias();
    });

    const averiaSeleccionada = (idAveria) => {
      arrayAverias.value.forEach((item) => {
        item.select = false;
        if (item.idAveria === idAveria) item.select = true;
      });
    };

    const tecnicos = async () => {
      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "tecnicos",
      });

      if (response.response) {
        arrayTecnicos.value = response.data.tecnicos;
      }
    };

    // CREAR TECNICO
    const crearTecnico = async () => {
      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "crearTecnico",
        idTecnico: uniqid() + uniqid(),
        tecnico: titulos.value.tecnico,
      });
      if (!response.response) alert("No Actualizado");
      if (response.response) {
        titulos.value.tecnico = "";
        tecnicos();
      }
    };

    // ACTUALIZA TECNICO
    const actualizarTecnico = async (item) => {
      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "actualizarTecnico",
        idTecnico: item.idTecnico,
        tecnico: item.tecnico,
        activo: item.activo,
      });
      if (!response.response) alert("No Actualizado");
      if (response.response) tecnicos();
    };

    const averias = async () => {
      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "averias",
      });

      if (response.response) {
        arrayAverias.value = response.data.averias;
        arraySoluciones.value = [];
        idAveria.value = "";
      }
    };

    // CREAR TECNICO
    const crearAveria = async () => {
      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "crearAveria",
        idAveria: uniqid() + uniqid(),
        averia: titulos.value.averia,
      });
      if (!response.response) alert("No Actualizado");
      if (response.response) {
        titulos.value.averia = "";
        arraySoluciones.value = [];
        idAveria.value = "";
        averias();
      }
    };

    // ACTUALIZA AVERIAS
    const actualizarAveria = async (item) => {
      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "actualizarAveria",
        idAveria: item.idAveria,
        averia: item.averia,
        activo: item.activo,
      });
      if (!response.response) alert("No Actualizado");
      if (response.response) {
        averias();
        arraySoluciones.value = [];
      }
    };

    const soluciones = async (idAveria) => {
      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "soluciones",
        idAveria: idAveria,
      });

      if (response.response) arraySoluciones.value = response.data.soluciones;
    };

    // CREAR TECNICO
    const crearSolucion = async () => {
      if (!idAveria.value.length) return;

      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "crearSolucion",
        idSolucion: uniqid() + uniqid(),
        idAveria: idAveria.value,
        solucion: titulos.value.solucion,
      });
      if (!response.response) alert("No Actualizado");
      if (response.response) {
        titulos.value.solucion = "";
        soluciones(idAveria.value);
      }
    };

    // ACTUALIZA SOLUCIONES
    const actualizarSolucion = async (item) => {
      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "actualizarSolucion",
        idSolucion: item.idSolucion,
        solucion: item.solucion,
        activo: item.activo,
      });
      if (!response.response) alert("No Actualizado");
      if (response.response) soluciones(item.idAveria);
    };

    return {
      titulos,
      idAveria,
      soluciones,
      averiaSeleccionada,
      arrayTecnicos,
      crearTecnico,
      actualizarTecnico,
      crearAveria,
      arrayAverias,
      actualizarAveria,
      arraySoluciones,
      crearSolucion,
      actualizarSolucion,
    };
  },
};
</script>

